.writing {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -moz-user-select: none;
    user-select: none;
    border-bottom: 80px;
    /* text-align: center; */
}
.writing, .ostara, .thesis, .ux-writing {
    padding-bottom: 30px;
}
.writing h1{
    padding-bottom:15px;
}
.writing h2{
    padding-bottom:10px;
}
.sub-heading{
    text-align: center;
    margin-bottom: 30px;
}
.title-container:hover{
    text-decoration: line-through;
    /* text-decoration-style: wavy; */
    text-decoration-color:rgba(118, 159, 202, 0.555);
    /* pointer-events: none; */
    /* background-color: rgba(118, 159, 202, 0.37); */
    
    /* width: fit-content;
    background-image: linear-gradient(to right, rgba(118, 159, 202) 0%, rgba(118, 159, 202, 0.37) 100%) */

}
.writing-button{
    border: 1px solid #979696;
    background-color:white;
    background: white;
    padding: 7px 30px 7px 30px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    margin: 4px 2px; 
    font-family: 'Lato';
    font-weight: 300;
    outline:0;
}
.writing-button:hover{
    background-color: black;
    color: white;
    
} 
.writing-project-left{
    margin: auto;
    width: 65%;

}
.writing-project-right{
    margin: auto;
    width: 65%;
    text-align: end;
}
.writing-title{
    padding-bottom: 20px;
}
.writing-content{
    text-align: justify;
}

.thesis a{
    color: #769FCA;
}

.ux-writing hr{
    width: 50%;
    margin-left:0;
}
.ostara .ostara-img{
    width: 30%;
    float: right;    
    margin: 65px 0 0 15px;
}
.write-error-button{
    border: 1px solid #979696;
    background-color:white;
    background: white;
    padding: 7px 30px 7px 30px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    margin: 4px 2px; 
    font-family: 'Lato';
    font-weight: 300;
    outline:0;
}
.write-error-button:hover{
    background-color: black;
    color: white;
}